<template>
  <div class="outer">
    <form id="novalidatedform" novalidate />
    <div class="patient-list-container">
      <div class="input-group add-patient p-2">
        <input type="text" class="form-control" placeholder="" v-show="false" v-model="searchPatient">
        <div class="input-group">
          <button id="btn-add-patient" type="button" v-on:click='addPatient'></button>
        </div>
      </div>
      <div class="patient-list p-3">
        <div class="mb-2" v-for="i in patientList" :key="i.pkey">
          <router-link class="card patient-card" :class="[parseSelected(i)?'patient-card-selected':'']" v-bind:to="'/watermeter/pkey/'+i.pkey">
            <div>
              <div class="inline" :class="[i.gender==='male'?'male':'',i.gender==='female'?'female':'']">
                {{i.bedno}} {{i.name}}
              </div>
              <button class="close" v-on:click="storePatient(i)">
                <span>&times;</span>
              </button>
              <div class="cga-data">
                <div class="text-sm"> {{getCGA(i)}} </div>
                <div class="text-xs text-muted"> {{i.number}}</div>
              </div>
              <div class="update-time text-xs text-muted">{{i.updateBy}}@{{parseUpdate(i.lastUpdate)}}</div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <div class="patient">
      <div id="information" class="p-3" v-if="!pkeyExist">

        <h4 class="pl-1">WaterMeter</h4>
        <ol>
          <li></li>
          <li>()</li>
          <li>/</li>
          <li></li>
          <li></li>
        </ol>
        <h4 class="pl-1 mt-3"></h4>
        <ol>
          <li></li>
          <li>inputPN</li>
          <li class="font-weight-bold text-primary">"PN"TPN sheet</li>
          <li>PN"prescribe"</li>
          <li></li>
        </ol>
        <h4 class="pl-1 mt-3"></h4>

        <ul>
          <li>2018-09-05 :</li>
          <li>2018-09-05 :</li>
          <li class="text-danger">2018-08-25 :</li>
          <li class="text-danger">2018-06-22 :TPN</li>
          <li>2018-06-22 :</li>
          <li>2018-06-21 :patient card()</li>
          <li>2018-06-15 :TrackerFeedingml/kg/day</li>
          <li>2018-06-15 :</li>
          <li>2018-06-15 :Tracker</li>
          <li>2018-06-12 :CaCentral for Ca</li>
          <li>2018-06-11 :(TPN)</li>
          <li>2018-06-06 :Tracer</li>
          <li>2018-06-05 :</li>
          <li>2018-06-05 </li>
        </ul>
        <h4 class="pl-1 mt-3">Special thanks:</h4>
        <ul>
          <li></li>
        </ul>
      </div>

      <div v-if="pkeyExist" class="h100">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import util from "@/util.js";
let database;
export default {
  name: "waterMeter",
  data() {
    return {
      searchPatient: ""
    };
  },
  computed: {
    patientList: function() {
      return this.$store.state.patientList;
    },
    pkeyExist: function() {
      return this.$store.getters.pkeyExist;
    }
  },
  methods: {
    addPatient: function() {
      let vm = this;
      let waterMeterRef = database.ref("/watermeter/");
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      waterMeterRef.push({
        hisid: "",
        name: "",
        bedno: "",
        gender: "",
        active: true,
        birthdate: util.getToday(),
        ga_week: "",
        ga_day: 0,
        lastUpdate: util.getNow(),
        updateBy: currentUser,
        watermeter: []
      });
    },
    deletePatient: function(pkey) {
      let r = confirm("?()");
      if (r == true) {
        let patientRef = database.ref("/watermeter/" + pkey + "/");
        patientRef.remove();
      }
    },
    storePatient: function(patient) {
      let pkey = patient.pkey;
      if (!patient.hisid && patient.name == "") {
        let r = confirm(
          ""
        );
        if (r == true) {
          let patientRef = database.ref("/watermeter/" + pkey + "/");
          patientRef.once("value", function(snapshot) {
            patientRef.remove();
          });
        }
        return;
      }
      let r = confirm(
        "?()"
      );
      if (r == true) {
        let patientRef = database.ref("/watermeter/" + pkey + "/");
        let storeRef = database.ref("/watermeter_store/");
        patientRef.once("value", function(snapshot) {
          let obj = snapshot.val();
          obj.deleteDate = util.getNow();
          storeRef.push(obj);
          patientRef.remove();
        });
      }
    },
    setRoute(route) {
      let vm = this;
      vm.$store.commit("setRoute", route);
    },
    parseUpdate(inputDateTime) {
      let split = inputDateTime.split(" ");
      let date = split[0].split("-").map(x => Number(x));
      date.splice(0, 1);
      date = date.join("/");
      let time = split[1].split(":");
      time.splice(2, 1);
      time = time.join(":");
      return date + " " + time;
    },
    parseSelected(input) {
      return input.pkey == this.$route.params.pkey;
    },
    getCGA(patientData) {
      let cga = util.getCGA(
        util.getNow(),
        patientData.birthdate,
        patientData.ga_week,
        patientData.ga_day
      );
      if (!cga) return "";
      return patientData.ga_week + "+" + patientData.ga_day + " > " + cga;
    }
  },
  beforeRouteUpdate(to, from, next) {
    let vm = this;
    vm.setRoute(to);
    if (to.params.pkey && vm.patientList.find(x => x.pkey == to.params.pkey)) {
      next();
    } else if (!to.params.pkey) {
      next();
    } else {
      next("/watermeter");
    }
  },
  mounted() {
    let vm = this;
    vm.setRoute(vm.$route);
    database = firebase.database();
    let waterMeterRef = database.ref("/watermeter/");
    waterMeterRef.on("value", function(snapshot) {
      vm.$store.commit("setSnapshot", snapshot.val());
    });
  }
};
</script>

<style scoped lang="scss">
.outer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.patient-list-container {
  flex-basis: 220px;
  flex-grow: 0;
  flex-shrink: 0;
  border-width: 2px;
  width: 200px;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.3);
}
.add-patient {
  flex: 0 0 auto;
}
#btn-add-patient {
  width: 100%;
  background-color: rgb(196, 219, 238);
  border: 0px;
  padding-top: 5px;
  padding-bottom: 5px;
  cursor: pointer;
  &:hover {
    background-color: rgb(186, 203, 218);
  }
  transition: all 0.3s;
}
.patient-list {
  overflow-x: hidden;
  overflow-y: scroll;
  flex: 1 1 auto;
}
.patient {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.patient-card {
  text-decoration: none !important;
  box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, 0.1);
  background-color: #f4f4ff;
  padding-left: 3px;
  transition: all 0.2s;
  color: hsla(0, 0, 0, 0.9);
  .male {
    color: hsl(216, 60%, 50%);
  }
  .female {
    color: hsl(332, 50%, 55%);
  }
}
.patient-card-selected {
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.5);
    background-color: white
}
.patient-card:hover {
  background-color: rgb(255, 255, 255);
}
.cga-data {
  color: rgb(116, 119, 177);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5px;
  padding-right: 15px;
  height: 16px;
  font-size: 0.85em;
}
.update-time {
  padding-left: 5px;
}
.h100 {
  height: 100%;
}
.w20 {
  width: 20%;
}
.w80 {
  width: 80%;
}
.inline {
  display: inline-block;
  white-space: nowrap;
}
.text-xs {
  font-size: 0.8em;
}
#information {
  height: 100%;
  overflow-y: scroll !important;
}

::-webkit-scrollbar {
  width: 0.8em;
  height: 0.8em;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(225, 225, 225, 1);
}

::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
}
</style>



